.btnAll {
  width: 120px;
  height: 40px;
}
.blockBtn {
  margin-top: 23px;
  margin-right: 15px;
}

.search {
  text-align: center;
  font: 400 27px 'Roboto';
  vertical-align: button;
  margin: 25px 15px 0 0;
}
.text {
  display: flex;
  vertical-align: middle;
}
input {
  margin: 33px 15px 0 0;
}

li {
  list-style-type: none;
  font: 400 16px 'Roboto';
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-top: 5px;
}
.arrow{
  cursor: pointer;
  color: crimson;
  vertical-align: middle;
}
.active {
  border-left: 3px solid crimson;
  height: 34px;
  width: 440px;
  padding-left: 2px;
}


.switch {
  opacity: 1;
  transition: opacity .25s ease;
}

body {
  overflow: hidden;
}
.App {
  width: 1350px;
  margin: 0 auto;
}
.item-search {
  display: flex;
  justify-content: flex-end;
}
.item-form {
  display: flex;
  justify-content: space-between;
}
.category {
  width: 42%;
}
.task {
  width: 58%;
}
.taskForm {
  display: flex;
  justify-content: flex-end;
}
.track-vertical {
  top: 2px;
  bottom: 2px;
  right: 2px;
  border-radius: 3px;
  background: #9F9F9F;
}
.thumb-vertical {
  position: relative;
  display: block;
  width: 100%;
  cursor: pointer;
  border-radius: inherit;
  background-color: #3F51B5; 
}



